const menu = document.querySelector('.navbar-mobile');
const openBtn = document.querySelector('.menu-btn');
const closeBtn = document.querySelector('.navbar-close-btn');
const navLinks = document.querySelectorAll('.navbar-nav-link'); // —É—Å—ñ –ø–æ—Å–∏–ª–∞–Ω–Ω—è –º–µ–Ω—é

// –í—ñ–¥–∫—Ä–∏—Ç–∏ –º–µ–Ω—é
openBtn.addEventListener('click', () => {
  menu.classList.add('is-open');
  document.body.classList.add('menu-open'); // –±–ª–æ–∫—É—î —Å–∫—Ä–æ–ª —Å—Ç–æ—Ä—ñ–Ω–∫–∏
  document.querySelector('body').style.overflow = 'hidden';
});

// –ó–∞–∫—Ä–∏—Ç–∏ –º–µ–Ω—é –ø–æ —Ö—Ä–µ—Å—Ç–∏–∫—É
closeBtn.addEventListener('click', () => {
  menu.classList.remove('is-open');
  document.body.classList.remove('menu-open');
  document.querySelector('body').style.overflow = 'auto';
});

// –ó–∞–∫—Ä–∏—Ç–∏ –º–µ–Ω—é –ø—Ä–∏ –∫–ª—ñ–∫—É –Ω–∞ –±—É–¥—å-—è–∫–µ –ø–æ—Å–∏–ª–∞–Ω–Ω—è
navLinks.forEach(link => {
  link.addEventListener('click', () => {
    menu.classList.remove('is-open');
    document.body.classList.remove('menu-open');
    document.querySelector('body').style.overflow = 'auto';
  });
});
